<template>
	<form @submit.prevent='updateData(item.id, elem)' class="renderInfo">
		<label :for="item.id">{{item.title | capitalize}}:</label>

		<textarea v-if='item.type === "textarea"' class="form-control" :id="item.id" v-model='elem.value'></textarea>
		<input v-else :id="item.id" class="form-control" v-model='elem.value' :type="elem.type">

		<button type="submit" class="admin-button">Оновити {{elem.title}}</button>
	</form>
	<!-- /.renderInfo -->
</template>
<script>
	export default {
		name: 'renderInfo',
		props: ['item'],
		data() {
			return {
				elem: {
					title: this.item.title,
					type: this.item.type,
					value: this.item.value,
				},
			}
			
		},
		methods: {
			updateData(id, elem) {
				const createdAt = new Date();
				this.$_db.collection('personal').doc(id).update({createdAt, ...elem });
			}
		},
		filters: {
			capitalize(value) {
				return value[0].toUpperCase() + value.slice(1);
			}
		}

	}
</script>
<style lang='sass' scoped="true">
	textarea
		height: 200px
</style>